.sidebar{
    background-color: #7CB9E8;
    color: white;
    padding-left: 10px;
    height: 100vh;
}



.top_side{
    display: flex;
    justify-content: space-around;
    align-items: center;
    border-bottom: .5px solid white;
}

.side_item{
    display: flex;
    gap: 5px;
    padding: 5px;
    align-items: center;
    color: rgba(255, 255, 255, 0.466);
    transition: .3s;
}
.side_item:hover{
    color: white;
    cursor: pointer;
}


.selected{
    color: white;
}