

 .input{
   width:100% ;
    padding: 12px 20px;
    margin: 8px 0;
    display: inline-block;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;


 }


 .btn{
border: none;
padding: 12px 20px;
background-color: #00308F;
border-radius: 4px;
box-sizing: border-box;
width: 100%;
color: white;
transition: .3s;
 }

 .btn:hover{
    opacity: .7;
    cursor: pointer;
 }

 .input_big{
    display: inline-block;
    border-radius: 4px;
    padding:40px 20px;
    width:100% ; 
    box-sizing: border-box;
    margin: 8px 0;
  padding-top: 10px;
  padding-left: 10px;
    border: 1px solid #ccc;
      }

input[type=text]:focus, select:focus ,[type=file]:focus{

 box-shadow: rgba(3, 102, 214, 0.3) 0px 0px 0px 3px;;
outline: none;


}
      
    