.questions {
  height: 100vh;
  overflow-y: scroll;
}
.questions .question {
  font-size: 1.5em;
  background: #e4e4e4;
  padding: 0.01em 1em;
  margin: 1em;
}
.questions .question:last-child {
  margin-bottom: 15rem;
}
.questions .question input {
  width: 100%;
  font-size: 1em;
  padding: 0.5em;
  margin: 0.5em 0;
}
.comments-container {
  display: flex;
  flex-direction: column;
  width: 96%;
  margin: 1em auto 15em;
}
.comments-field {
  width: 100%;
  height: 100px;
  padding: 10px;
  margin-bottom: 1em;
}
.send-comments-button {
  right: 0;
  margin-left: auto;
  padding: 15px;
  border-radius: 5px;
  cursor: pointer;
}
.modify-survey-button-container {
  display: flex;
  justify-content: flex-end;
  margin-top: 1em;
  width: 96%;
  margin: 0 auto;
}
.modify-survey-button {
  margin-left: auto;
  padding: 15px;
  border-radius: 5px;
  cursor: pointer;
  margin-bottom: 10rem;
}
